<template>
  <div class="admin-info">
      <h1>Info</h1>
      <div class="card-wrapper">
        <div class="card-1">
            <MyCard 
                headerBg="pink" 
                headerText="black"
                contentText="black"
            >
                <template  v-slot:title>
                    <h1>Weekly visitor</h1>
                </template>
                <template  v-slot:content>
                    <LineChart 
                        v-if="loaded" 
                        class="line-chart" 
                        :width="200"
                        :height="300"
                        :chartData="lineData"
                        :options="chartOptions" 
                    />
                    <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Alias pariatur corrupti rerum facilis quis repudiandae labore id, repellat cupiditate odio sint quisquam deserunt quidem harum obcaecati vero ipsum tenetur sapiente?</p>
                </template>
            </MyCard>
        </div>
        <div class="card-2">
            <MyCard
                headerBg="primary"
                contentText="black"
            >
                <template v-slot:title>
                    <h1>Log Activity</h1>
                </template>
                <template v-slot:content>
                    <MyList
                        :data="logsData"
                    />
                </template>
            </MyCard>
        </div>
        <div class="card-3">
            <MyCard>
                <template v-slot:title>
                    <h1>Recent Add book</h1>
                </template>
                <template v-slot:content>
                    <div v-if="recentBook.length == 0" class="empty-data">
                        <h3 class="txt-danger">No Result found..</h3>
                    </div>
                    <div v-else class="table-wrapper">
                        <MyTable
                        headerBg="pink"
                        headerTxt="white"
                        >
                        <template v-slot:head>
                            <tr>
                                <td>Name</td>
                                <td>Author</td>
                                <td>User Add</td>
                                <td>Date</td>
                            </tr>
                        </template>
                        <template v-slot:data>
                            <tr v-for="(book,index) in recentBook" :key="index" >
                                <td>{{ book.name }}</td>
                                <td>{{ book.author ? book.author.name : 'Unknown' }}</td>
                                <td>{{ book.user_add }}</td>
                                <td>{{ setDate(book.date) }}</td>
                            </tr>
                        </template>
                    </MyTable>
                    <button 
                        class="btn btn-primary mt-3"
                        @click="goTo('admin-book')"
                    >
                        See Details
                    </button>
                    </div>
                </template>
            </MyCard>
        </div>
        <div class="card-4">
            <MyCard>
                <template v-slot:title>
                    <h1>Book Add</h1>
                </template>
                <template v-slot:content>
                    <h4>Amount book has added in last 3 month</h4>
                    <BarChart  
                        v-if="loaded" 
                        :width="300"
                        :height="300"
                        :options="chartOptions"
                        :chartData="barData" 
                    />
                </template>
            </MyCard>
        </div>
      </div>
  </div>
</template>

<script>
import { 
    getAmountVisitor, 
    getAddBook, 
    getAllLogs, 
    getAllBooks, 
    setDate
} from "@/utils/index"
import { listMonth } from "@/constant/index";

export default {
    name : 'adminInfo',
    components : {
        MyCard : () => import('@/components/admin/CardItem'),
        LineChart : () => import('@/components/MyLineChart'),
        MyList : () => import("@/components/admin/ListCard"),
        BarChart : () => import("@/components/MyBarChart"),
        MyTable : () => import("@/components/MyTable"),
    },
    data() {
        return {
            loaded : false,
            colorList : ['#89ff33','#0a70cf','#0bb2eb','#2f82d5','#c7a1ba','#ff3389','#59f4e1'],
            logsData : [],
            recentBook : [],
            lineData : {
                labels : ['Jumlah','Pengunjung'],
                datasets : [],
                options : {
                    responsive: true,
                    maintainAspectRatio: false,
                    animation: {
                        duration: 0
                    },
                    hover: {
                         animationDuration: 0
                    },
                    responsiveAnimationDuration: 0
                }
            },
            barData : {
                label : ['Jumlah','Buku'],
                datasets : [],
            },
            chartOptions : {
                responsive : true,
                    maintainAspectRatio: false,
                    animation: {
                        duration: 0
                    },
                    hover: {
                         animationDuration: 0
                    },
                    responsiveAnimationDuration: 0,       
            },
        }
    },
    inject : ['loading'],
    async created() {
        this.loaded = false;
        try {
            this.loading.value = true;
            // Get & Set amount visitor
            const amountVisitor = await getAmountVisitor();
            this.setDataVisitor(amountVisitor);
            //  Get & Set data addbook
            const dataAddBook = await getAddBook();
            this.setDataAddBook(dataAddBook);
            //  Get & Set logs activity
            this.logsData = await getAllLogs();
            // Get & Set recent add book
            this.recentBook = await getAllBooks();
            setTimeout( () => this.loading.value = false,1000);
            this.loaded = true;
        }catch(err) {
            console.log(err);
        }
    },
    methods :{
        setDataVisitor(data) {
            let datasets = [];
            data.map( (data,i) => {
                const d = new Date(data.date);
                const fullDate = `${d.getDate()}-${listMonth[d.getMonth()]}`;
                datasets.push({
                    label : fullDate,
                    backgroundColor : this.colorList[i],
                    data : [0,data.count],
                });
            });
            this.lineData.datasets = datasets;
        },
        setDataAddBook(data) {
            let datasets = [];
            data.map( (data,i) => {
                datasets.push({
                    label : data.month,
                    backgroundColor : this.colorList[i],
                    data : [data.count]
                });
            });
            this.barData.datasets = datasets;
        },
        setDate : function(date) {
            return setDate(date);
        },
        goTo(name) {
            this.$router.push({
                name,
            })
        }
    },
}
</script>

